import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
	import nongchanpin from '@/views/modules/nongchanpin/list'
	import news from '@/views/modules/news/list'
	import discussnongchanpin from '@/views/modules/discussnongchanpin/list'
	import jiufentousu from '@/views/modules/jiufentousu/list'
	import coupon from '@/views/modules/coupon/list'
	import chanpinleixing from '@/views/modules/chanpinleixing/list'
	import yonghu from '@/views/modules/yonghu/list'
	import orders from '@/views/modules/orders/list'
	import storeup from '@/views/modules/storeup/list'
	import config from '@/views/modules/config/list'
	import shangjia from '@/views/modules/shangjia/list'
	import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
	path: '/',
	name: '',
	component: Index,
	children: [{
		// main
		path: '/',
		name: '',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: '',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/nongchanpin',
		name: '',
		component: nongchanpin
	}
	,{
		path: '/news',
		name: '',
		component: news
	}
	,{
		path: '/discussnongchanpin',
		name: '',
		component: discussnongchanpin
	}
	,{
		path: '/jiufentousu',
		name: '',
		component: jiufentousu
	}
	,{
		path: '/coupon',
		name: '',
		component: coupon
	}
	,{
		path: '/chanpinleixing',
		name: '',
		component: chanpinleixing
	}
	,{
		path: '/yonghu',
		name: '',
		component: yonghu
	}
	,{
		path: '/orders/:status',
		name: '',
		component: orders
	}
	,{
		path: '/storeup',
		name: '',
		component: storeup
	}
	,{
		path: '/config',
		name: '',
		component: config
	}
	,{
		path: '/shangjia',
		name: '',
		component: shangjia
	}
	,{
		path: '/newstype',
		name: '',
		component: newstype
	}
	]
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.VueRouter  
const router = new VueRouter({
	mode: 'hash',
	/*hashhistory*/
	routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
