<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="chanpinmingcheng" >
					<el-input v-model="ruleForm.chanpinmingcheng" placeholder="" clearable  :readonly="ro.chanpinmingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="chanpinmingcheng" >
					<el-input v-model="ruleForm.chanpinmingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="chanpinleixing" >
					<el-input v-model="ruleForm.chanpinleixing" placeholder="" clearable  :readonly="ro.chanpinleixing"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="chanpinleixing" >
					<el-input v-model="ruleForm.chanpinleixing" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.tupian" label="" prop="tupian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.tupian?ruleForm.tupian:''"
						@change="tupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.tupian" label="" prop="tupian" >
					<img v-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shangjiaxingming" >
					<el-input v-model="ruleForm.shangjiaxingming" placeholder="" clearable  :readonly="ro.shangjiaxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shangjiaxingming" >
					<el-input v-model="ruleForm.shangjiaxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="tousuneirong" >
					<el-input v-model="ruleForm.tousuneirong" placeholder="" clearable  :readonly="ro.tousuneirong"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="tousuneirong" >
					<el-input v-model="ruleForm.tousuneirong" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="tousuriqi" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.tousuriqi" 
						type="datetime"
						:readonly="ro.tousuriqi"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.tousuriqi" label="" prop="tousuriqi" >
					<el-input v-model="ruleForm.tousuriqi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuxingming" >
